.overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.popup {

  background: #FFE3E5;
  border-radius: size(60px);
  max-width: size(700px);
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 99;
  transform: translate(-50%, -50%);
  padding: 0 size(7px);

  @include breakpoint(lg) {
    max-width: size(590px);
  }

  @include breakpoint(md) {
    max-width: size(440px);
  }

  @include breakpoint(sm) {
    max-width: size(350px);
  }

  @include breakpoint(ssm) {
    max-width: size(308px);
  }

  h3 {
    font-family: "Circe-Bold", 'Arial', sans-serif;
    max-width: size(500px);
    margin: 0 auto;
    text-align: center;
    padding: size(50px) 0 size(25px);
    letter-spacing: -1px;
    font-weight: 700;
    font-size: $text-size55;
    line-height: size(65px);
    color: #BC1A1D;

    @include breakpoint(md) {
      font-size: size(44px);
      line-height: size(47px);
    }

    @include breakpoint(sm) {
      font-size: size(33px);
      line-height: size(38px);
    }

    @include breakpoint(ssm) {
      font-size: size(24px);
      line-height: size(30px);
      padding: size(90px) 0 size(15px);
    }
  }

  p {
    font-family: "Circe-Regular", 'Arial', sans-serif;
    max-width: size(500px);
    margin: 0 auto size(50px);
    text-align: center;
    font-size: $text-size36;
    line-height: size(45px);

    color: $main-text-color;

    @include breakpoint(md) {
      font-size: size(28px);
      line-height: size(34px);
    }

    @include breakpoint(sm) {
      font-size: size(23px);
      line-height: size(28px);
    }

    @include breakpoint(ssm) {
      font-size: size(20px);
      line-height: size(24px);
      margin: 0 auto size(35px);
    }
  }

  // .popup__btn

  &__btn {
    color: $white-text-color;
    font-family: 'Arial', sans-serif;
    font-size: $text-size20;
    line-height: size(26px);
    background: $btn-link-color;
    padding: size(21px) size(10px);
    max-width: size(330px);
    display: block;
    width: 100%;
    margin: 0 auto size(40px);

    @include breakpoint(sm) {
      max-width: size(270px);
    }

    @include breakpoint(ssm) {
      max-width: size(200px);
    }
  }

  .close {
    position: absolute;
    right: size(29px);
    top: size(23px);
  }


}